.benefit {
  padding-bottom: 150px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__title {
    text-align: center;
  }

  &__items {

    &--border {
      border: 1px solid #000;
      border-radius: 50%;
      padding: 35px 28px 35px 30px;
      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2%);
      margin-right: 50px;
    }
  }

  &__item {
    display: flex;
    max-width: 360px;
    width: 100%;
    margin-bottom: 150px;

    &:nth-child(3) {
      margin-bottom: 0;
    }

    &--mr {
      margin-left: -50px;
    }

    &--ml {
      margin-left: 50px;
    }

    &--width {
      max-width: 400px;
    }
  }

  &__item-img {
    max-width: 131px;
    height: 60px;
    width: 100%;
  }

  &__item-desc {
    margin-left: 20px;
  }

  &__item-title {
    font: var(--font-secondtitle);
    margin: 0;
    margin-bottom: 20px;
  }

  &__item-text {
    font: var(--font-menu);
    margin: 0;
    line-height: 24px;
  }
}

@media (max-width: 1280px) {
  .benefit__items--border {
    order: -1;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  .benefit__wrapper {
    flex-direction: column;
    align-items: flex-start;
  }

  .benefit__item--ml,
  .benefit__item--mr {
    margin-left: 0;
  }

  .benefit__item {
    max-width: 100%;
    margin-bottom: 50px;
  }

  .benefit__item:nth-child(3) {
    margin-bottom: 50px;
  }

  // .benefit__items {
  //   margin-left: 100px;
  // }
}

@media (max-width: 991px) {
  .benefit__items {
    margin-left: 0;
  }
}

@media (max-width: 768px) {
  .benefit {
    padding-bottom: 50px;
  }
}

@media (max-width: 545px) {

  .benefit {
    padding-bottom: 0;
  }

  .benefit__item-img {
    max-width: 70px;
  }

  .benefit__item-title {
    font: var(--font-secondsubtitle);
  }

  .benefit__item-text {
    font: var(--font-menu-text);
  }

  .benefit__items--border {
    width: 265px;
    padding: 15px 11px 15px 15px;
    margin-left: auto;
    margin-right: auto;
  }
}
