.breadcrumbs {

  &__list {
    display: flex;
    justify-content: flex-start;
    margin: 0;
    margin-bottom: 95px;
  }

  &__item {
    &:not(:first-child) {
      margin-left: 20px;
      position: relative;

      &:last-child::before {
        content: '';
        position: absolute;
        top: 50%;
        left: -12px;
        width: 6px;
        height: 8px;
        background-image: url(../img/arr.svg);
        background-repeat: no-repeat;
        background-size: cover;
        display: block;
        transform: translateY(-50%);
      }
    }
  }

  &__link {
    color: var(--color-silver);

    &--active {
      color: var(--color-black);
    }
  }
}


@media (max-width: 545px) {
  .breadcrumbs {
    display: none;
  }
}
