.blog {

  padding-bottom: 150px;

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
  }

  &__item {
    max-width: 410px;
    width: 100%;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2%);
    border-radius: 20px;
  }

  &__item-desc {
    padding: 30px 30px 40px;
  }

  &__item-img {
    max-width: 100%;
  }

  &__item-name {
    font: var(--font-secondsubtitle);
    margin: 0;
    margin-bottom: 20px;
  }

  &__item-text {
    font: var(--font-text);
  }

  &__item-link {
    font: var(--font-text);
    color: var(--color-shiraz);
  }
}


@media (max-width: 992px) {
  .blog__items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {

  .blog {
    padding-bottom: 50px;
  }


  .blog__items {
    grid-template-columns: auto;
    justify-content: center;
  }

  .blog__item {
    margin-bottom: 20px;
  }
}

@media (max-width: 768px) {
  .blog__item-text {
    font: var(--font-menu-text);
  }
}
