.footer {
  background-color: var(--color-athensGray);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-color: var(--color-footer);
  }

  &__social {
    display: flex;
  }
}

.footer-top {
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 50px;

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font: var(--font-text);

    address {
      font-style: normal;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    .intro__social::before {
      display: none;
    }

    .intro__social-link:not(:last-child) {
      margin-bottom: 0;
    }

    .intro__social-link:not(:first-child) {
      margin-left: 30px;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
  }

  &__list {
    margin-top: 0;

    &:not(:first-child) {
      margin-left: 90px;
    }
  }

  &__item-title {
    font: var(--font-title-footer);
    margin-bottom: 20px;
  }

  &__link {
    font: var(--font-text);
    color: var(--color-black);
    transition: color .3s ease-in-out;

    &:hover {
      color: var(--color-shiraz);
    }
  }

  &__tel {
    font: var(--font-text-tel);
    display: flex;
    flex-direction: column;
  }

  &__tel-number {
    color: var(--color-black);
    margin-bottom: 10px;
  }

  &__tel-link {
    font: var(--font-menu);
    color: var(--color-shiraz);
    text-decoration: underline;
    text-align: center;
  }
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  z-index: 2;
  position: relative;
  padding: 20px 0;
  flex-wrap: wrap;
  font: var(--font-text);
  font-weight: 300;

  &__text {
    color: var(--color-black);
    transition: background-color .3s ease-in-out, color .3s ease-in-out;

    &:hover {
      color: var(--color-shiraz);
    }
  }
}

@media (max-width: 1200px) {
  .footer__social {
    flex-direction: column;
    align-items: center;
  }

  .footer-top__item .intro__social-link:not(:first-child) {
    margin-left: 0;
    margin-top: 15px;
  }
}

@media (max-width: 992px) {
  .footer-top {
    flex-wrap: wrap;
  }

  .footer-top__nav {
    order: 1;
    width: 100%;
    justify-content: space-around;
    margin-top: 20px;
  }

  .footer__social {
    flex-direction: row;
    align-items: center;
  }

  .footer-top__item .intro__social-link:not(:first-child) {
    margin-left: 15px;
    margin-top: 0;
  }

  .footer-top__item {
    flex-direction: row;
  }

  .footer-top__item .logo {
    margin-right: 20px;
  }

  .footer-bottom {
    justify-content: space-around;
    text-align: center;
  }
}


@media (max-width: 768px) {
  .footer-top {
    position: relative;
  }

  .footer__social {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .footer-top__item .logo {
    position: relative;
  }

  .footer-top__nav {
    flex-wrap: wrap;
  }
}


@media (max-width: 656px) {
  .footer::before {
    height: 85px;
  }
}

@media (max-width: 565px) {
  .footer-top__nav {
    justify-content: space-between;
  }

  .footer-top__list:not(:first-child) {
    margin-left: 0;
  }

  .footer-top__list:nth-child(3) {
    margin-top: 30px;
    margin-bottom: 40px;
  }

  .footer-top__list:nth-child(2) {
    margin-right: 50px;
  }

  .footer-top__item .logo {
    margin-right: 0;
  }

  .footer-bottom {
    padding: 70px 0 20px;
  }

  .footer::before {
    height: 120px;
  }
}
