.intro {
  padding-bottom: 50px;
  background-color: #FBFBFD;
  position: relative;
  z-index: 0;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__desc {
    max-width: 510px;
    width: 100%;
  }

  &__title {
    color: var(--color-shiraz);
  }

  &__text {
    font: var(--font-text-light);
    color: var(--color-black);
    padding-bottom: 80px;
  }

  &__btn {
    background: var(--color-shiraz-gradient);
    color: var(--color-white);
    padding: 22px 67px;
    text-align: center;
    border-radius: 30px;
    font: var(--font-menu);
    transition: box-shadow .3s ease-in-out;

    &:hover {
      box-shadow: 2px 4px 3px rgba(170, 6, 48, .5%);
    }
  }

  &__img {
    max-width: 490px;
    max-height: 495px;
    width: 100%;
    height: 100%;
    position: relative;
    text-align: center;

    img {
      border-radius: 20px;
      margin-bottom: 10px;
    }

    &::before {
      content: '';
      position: absolute;
      top: -30px;
      right: -30px;
      width: 100%;
      height: 100%;
      background-color: var(--color-shiraz);
      border-radius: 20px;
      z-index: -1;
    }

    &-text {
      font: var(--font-menu);
    }
  }

  &__social {

    &::before {
      content: '';
      top: 0;
      left: 50%;
      width: 1px;
      height: 45px;
      background: var(--color-shiraz-gradient);
      display: block;
      position: relative;
      margin-bottom: 30px;
    }

    &-link {
      width: 40px;
      height: 40px;
      display: block;
      background-color: var(--color-silver);
      border-radius: 50%;
      transition: background-color .3s ease-in-out;

      &:hover {
        background-color: var(--color-shiraz);
      }

      svg {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        pointer-events: none;
      }

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }
}

@media (max-width: 1185px) {
  .intro__social {
    display: none;
  }

  .intro__img::before {
    display: none;
  }
}

@media (max-width: 768px) {
  .intro {
    padding-bottom: 50px;
  }

  .intro__img {
    position: absolute;
    width: 100%;
    max-width: 100%;

    left: 0;
    z-index: -1;
    background-image: url(../img/wom.png);
    background-repeat: no-repeat;
    background-size: cover;

    img {
      width: 100%;
      max-width: 100%;
      border-radius: 0;
      display: none;

    }
  }

  .intro__title {
    font: var(--font-subtitle);
    max-width: 240px;
    text-shadow: 1px 1px 1px #fff;
    text-align: start;
  }

  .intro__text {
    display: none;
  }

  .intro__btn {
    padding: 12px 23px;
  }

  .intro__desc {
    padding-top: 70px;
    padding-left: 45px;
    height: 505px;
  }
}

.intro__img-text {
  position: absolute;
  left: 50%;
  bottom: -30px;
  transform: translateX(-50%);
  width: 100%;
}

@media (max-width: 545px) {
  .intro__desc {
    padding-top: 40px;
    padding-left: 20px;
  }

  .intro__btn {
    padding: 9px 33px;
    font: var(--font-text);
  }
}
