.cart {
  position: relative;
  margin-left: 10px;

  &__btn {
    position: relative;
    width: 36px;
    height: 26px;
    fill: #000;

    &:hover {
      fill: var(--color-shiraz);
    }

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      pointer-events: none;

    }

    &:not(:first-child) {
      margin-left: 10px;
    }


  }

