.works {
  position: relative;
  padding-bottom: 100px;
  margin-bottom: 150px;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 390px;
    background-color: var(--color-athensGray);
    z-index: -2;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-top: 130px;
  }

  &__video {
    z-index: 1;
    max-width: 740px;
    width: 100%;
  }

  &__video-desc {
    max-width: 320px;
    width: 100%;
    margin-top: 60px;
    margin-left: 80px;
  }

  &__video-title {
    font: var(--font-secondsubtitle);
    color: var(--color-shiraz);
    margin: 0;
    margin-bottom: 16px;
  }

  &__video-text {
    font: var(--font-text);
  }

  &__items {
    width: 740px;
    background-color: var(--color-athensGray);
    padding: 40px 68px 40px 260px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2%);
    position: absolute;
    right: 0;
    top: 40px;
    border-radius: 20px;
  }

  &__item {
    margin-bottom: 20px;
  }

  &__item-title {
    font: var(--font-secondtitle);
    color: var(--color-shiraz);
    margin: 0;
    margin-bottom: 10px;

  }

  &__item-text {
    font: var(--font-menu);
    line-height: 24px;
  }
}

.video-js {
  height: 431px;
  width: 100%;
}

@media (max-width: 1280px) {
  .works__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .works__items {
    position: relative;
    width: 100%;
    padding: 20px 40px;
    box-shadow: none
  }

  .works__video-desc {
    max-width: 100%;
    margin-left: 0;
    padding-left: 5px;
  }

  .works::after {
    display: none;
  }
}

@media (max-width: 768px) {

  .works {
    padding-bottom: 0;
    margin-bottom: 80px;
  }

  .works__wrapper {
    padding-top: 0;
  }

  .works__items {
    width: 105%;
    padding: 20px;
    box-shadow: none;
    border-radius: 0;
  }

  .video-js {
    max-width: 500px;
    height: 281px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 545px) {
  .works {
    margin-bottom: 50px;
  }

  .works__items {
    top: 0;
  }

  .works__item-title {
    font: var(--font-secondsubtitle);
  }

  .works__item-text {
    font: var(--font-menu-text);
  }

  .works__video-desc {
    margin-top: 20px;
  }
}
