.nav__list {
  display: flex;
  align-items: center;
  margin: 0;
}

.nav__item:not(:first-child) {
  margin-left: 60px;
}

.nav__item {
  position: relative;
  padding: 15px 0;
}

.nav__link {
  color: var(--color-black);
  transition: background-color .3s ease-in-out, color .3s ease-in-out;

  &:hover {
    color: var(--color-shiraz);
  }
}

.mobile-back {
  display: none;
}

.burger {
  width: 28px;
  height: 15px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: relative;
  display: none;
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: #000;
}

.burger::after {
  top: auto;
  bottom: 0;
}

.burger span {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  display: inline-block;
  background-color: #000;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
}

.lock {
  overflow: hidden;
}

@media (max-width: 768px) {
  .burger {
    display: block;
  }

  .mobile-back {
    display: block;
  }

  .nav {
    position: fixed;
    left: 0;
    top: 0;
    max-width: 400px;
    width: 80%;
    height: 100vh;
    z-index: 100;
    box-shadow: 5px 0 15px -5px rgba(0, 0, 0, 0.8);
    background-color: #fff;
    transform: translateX(-150vw);
    overflow-x: hidden;
    overflow-y: auto;
  }

  .nav__list {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    padding-top: 10%;
    position: relative;
  }

  .nav__item {
    margin-left: 0 !important;
    font-size: 20px;
    width: 100%;
    padding: 0;
    position: static;
  }

  .nav__link {
    padding: 20px 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #bfbfbf;
  }

  .nav__list.transformation {
    transform: translateX(-100%);
    transition: all 0.3s;
  }

  .nav.open {
    transform: translateX(0);
    transition: all 0.3s;
  }

  .overlay.open {
    opacity: 1;
    visibility: visible;
    transition: all 0.3s;
  }

  .mobile-back a {
    position: relative;
    border: none;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}
