.rewiews {
  padding-bottom: 150px;

  &__item {
    padding: 40px 30px;
    border-radius: 20px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2%);
    margin: 5px;
  }

  &__item-desc {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }

  &__item-name {
    font: var(--font-text-medium);
    margin: 0;
    margin-bottom: 20px;
  }

  &__item-date {
    font: var(--font-text-date);
    color: var(--color-shiraz);
  }

  &__item-text {
    font: var(--font-text);
    margin: 0;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.swiper-button-prev,
.swiper-button-next {
  background-color: #e5e5e5;
  width: 32px;
  height: 32px;
  border-radius: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 7px 12px;
  border: none;

}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.swiper-button-prev {
  background-image: url("../img/arr.svg");
  transform: rotate(-180deg);
  transition: color .3s ease-in-out;
  position: static;
  margin: 0;
  margin-right: 10px;
}

.swiper-button-next {
  background-image: url("../img/arr-r.svg");
  transition: color .3s ease-in-out;
  background-color: var(--color-shiraz);
  position: static;
  margin: 0;
  margin-left: 10px;
}

.slider-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 32px;
  margin-top: 60px;
}

.slider-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 32px;
}

.swiper-pagination {
  position: relative;
}

/* swiper-bullets styling */
.swiper-pagination-bullet {
  width: 16px;
  height: 16px;
  margin: 0 15px;
  background-color: #e5e5e5;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: var(--color-shiraz);
  position: relative;

  &::after {
    content: '';
    top: 50%;
    left: 50%;
    width: 22px;
    height: 22px;
    border: 1px solid var(--color-shiraz);
    border-radius: 50%;
    display: block;
    position: absolute;
    transform: translate(-50%, -50%);
    padding: 5px;
  }
}

@media (max-width: 768px) {
  .rewiews {
    padding-bottom: 50px;
  }
}

@media (max-width: 545px) {

  .rewiews .title {
    padding-bottom: 20px;
  }

  .rewiews__item-name {
    font: var(--font-num-medium);
  }

  .rewiews__item {
    padding: 30px 25px;
  }
}
