.header {
  background-color: #FBFBFD;
  position: relative;
  z-index: 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 80px;
  position: relative;
}

@media (max-width: 768px) {

  .header {
    z-index: 100;
  }

  .header__container {
    padding-top: 55px;
  }

  .logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}


@media (max-width: 545px) {
  .header__container {
    padding-bottom: 40px;
  }
}
