.product {
  padding-bottom: 155px;
  padding-top: 130px;

  .product__wrapper {
    padding-bottom: 150px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    row-gap: 60px;
    column-gap: 60px;
    padding-top: 60px;
    margin-bottom: 60px;
  }

  &__item {
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    // filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.2));
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2%);
    border-radius: 20px;
    padding: 50px 30px 45px;

    &-btn {
      width: 42px;
      height: 42px;

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__item-img {
    text-align: center;
  }

  &__item-title {
    font: var(--font-subtitle);
    color: var(--color-shiraz);
    margin: 0;
    margin-top: 30px;
    margin-bottom: 10px;
  }

  &__item-text {
    display: inline-block;
    font: var(--font-text);
    margin-bottom: 35px;
  }

  &__item-sale {
    font: var(--font-text-bold);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__btn {
    display: block;
    justify-content: center;
    width: 210px;
    margin: 0 auto;
    background-color: var(--color-white);
    color: var(--color-black);
    padding: 22px;
    text-align: center;
    border: 1px solid var(--color-shiraz);
    border-radius: 30px;
    font: var(--font-menu);
    transition: background-color .3s ease-in-out, color .3s ease-in-out;

    &:hover {
      background-color: var(--color-shiraz);
      color: var(--color-white);
    }
  }
}

.product .product__item {
  margin-right: 15px;
  margin-left: 15px;
}

.product__mass {
  font: var(--font-text-date);

  .product__btn {
    padding: 2px 7px;
    display: inline-block;
    width: 44px;
    font: var(--font-text-date);
    margin-left: 5px;
  }
}

@media (max-width: 1200px) {
  .product__items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .product__item-sale {
    display: none;
  }
}


@media (max-width: 768px) {
  .product__items {
    grid-template-columns: repeat(1, 1fr);
  }

  .product {
    padding-bottom: 50px;
    padding-top: 25px;

    .product__wrapper {
      padding-bottom: 50px;
    }
  }
}

.slider-container,
.slider-container-1,
.slider-container-2 {
  overflow: hidden;
  width: 100%;
}

@media (max-width: 545px) {

  .product {
    padding-bottom: 0px;

    .product__wrapper {
      padding-bottom: 0px;
    }
  }

  .catalog__prop-checkboxes {
    position: relative;
  }

  .product__wrapper .catalog__caption {
    position: absolute;
    right: 0;
    top: 35px;
    padding-top: 0;
  }

  .product__wrapper .title {
    padding-bottom: 0;
  }

  .product__item-title {
    font: var(--font-secondsubtitle);
  }

  .product__item-text {
    font: var(--font-menu-text);
  }

  .product__btn {
    display: none;
  }

  .product__items {
    column-gap: 0px;
  }

  .product__item {
    width: 100%;
    margin: 0;
    flex-shrink: 0;
    padding: 20px 15px 40px;
  }

  .slider-container .swiper-wrapper,
  .slider-container-1 .swiper-wrapper,
  .slider-container-2 .swiper-wrapper {
    display: flex;
    justify-content: initial;
    flex-wrap: nowrap;
  }
}
