.card {

  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding-top: 75px;
    padding-bottom: 150px;
  }

  &__img {
    max-width: 470px;
    width: 100%;
    display: flex;
    margin-left: 60px;

    img {
      object-fit: cover;
      width: 100%;
    }
  }

  &__desc {
    max-width: 540px;
    width: 100%;

    .title {
      font: var(--font-title-medium);
      color: var(--color-shiraz);
      margin: 0;
      margin-bottom: 30px;
    }

    &-bottom {
      display: flex;
      justify-content: flex-end;
    }
  }

  &__text {
    font: var(--font-menu);
    line-height: 150%;
    margin: 0;
    margin-bottom: 20px;
  }
}

.card-see {
  padding-bottom: 150px;
}

.catalog {

  &__prop-checkboxes {
    padding-top: 60px;
    border-top: 1px solid var(--color-silver);
    display: flex;
    align-items: center;
    padding-bottom: 75px;
  }

  &__caption {
    font: var(--font-price);
    text-transform: uppercase;
    margin-left: auto;
  }
}

.counter {
  border: 1px solid var(--color-shiraz);
  border-radius: 30px;
  display: inline-block;
  padding: 18px 23px;
  margin-right: 60px;

  button,
  [type="button"],
  [type="reset"],
  [type="submit"] {
    border: none;
    background-color: transparent;
    font: var(--font-menu);
    font-weight: bold;
  }
}

.card__num {
  all: unset;
  -moz-appearance: textfield;
  width: 50px;
  text-align: center;
  font: var(--font-num-medium);

}

.card__num::-webkit-inner-spin-button {
  display: none;
}

.coun {
  display: none;

  button,
  [type="button"],
  [type="reset"],
  [type="submit"] {
    border: 1px solid var(--color-shiraz);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    background-color: transparent;
  }

  .card__num {
    width: 30px;
  }
}

.block {
  display: flex;
}

.block+#butt {
  display: none;
}


.product__item-sale-mob {
  display: none;
}

@media (max-width: 1180px) {
  .card__img {
    max-width: 350px;
  }

  .card__img img {
    object-fit: contain;
  }
}

@media (max-width: 992px) {
  .card__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .card__img {
    margin-left: 0;
    margin-bottom: 30px;
  }
}

@media (max-width: 768px) {
  .card-see .product__item-sale-mob {
    display: block;
  }
}


@media (max-width: 545px) {
  .card__wrapper {
    padding-top: 30px;

    .catalog__caption {
      position: absolute;
      right: 0;
      top: 35px;
      padding-top: 0;
    }

    .counter {
      padding: 7px;
      padding-bottom: 10px;
      margin-right: auto;
    }

    .card__num {
      width: 35px;
    }

    .custom-checkbox__content {
      padding-right: 10px;
      padding-left: 35px;
    }
  }

  .product__item-sale-mob {
    display: block;

    .catalog__prop-checkboxes {
      flex-wrap: wrap;
    }

    .counter {
      padding: 7px;
      padding-bottom: 10px;
      margin-right: auto;
    }

    .card__num {
      width: 35px;
    }

    .custom-checkbox__content {
      padding-right: 10px;
      padding-left: 35px;
    }
  }

  .mob-none {
    display: none;
  }

  .catalog__prop-checkboxes {
    border-top: none;
    padding-top: 0;
  }

  .catalog__caption {
    font-size: 28px;
    font-weight: 500;
  }

  .card-see {
    padding-bottom: 0;
  }
}
